@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
@import url(https://cdnjs.cloudflare.com/ajax/libs/nvd3/1.8.1/nv.d3.min.css);
@import url(https://unpkg.com/ionicons@4.2.2/dist/css/ionicons.min.css);

.multiselect-container span{
    padding: 2px 5px;
    background: #626262;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 2px;
    display: inline-flex;
    align-items: center;
    font-size: 10px;
    color: #fff;
    white-space: nowrap;
}
.optionListContainer{
	max-width: 250px;
}
.optionListContainer ul li{
	padding: 6px 10px;
}
.optionListContainer ul li.highlight {
    background: #626262;
    color: #ffffff;
	font-size: 10px;
}
.optionListContainer ul li:hover {
    background: #dbdbdb;
    color: #444444;
    cursor: pointer;
}
.table-detail-video th, .table-detail-video td {
    border: 0px !important;
}
.detail-images {
    margin-bottom: 10px;
}
.detail-images h6 {
    position: absolute;
    top: 8px;
    font-size: 10px;
    background: rgb(0 0 0 / 59%);
    color: #fff;
    padding: 5px 15px;
}
.detail-images img {
    height: 130px;
}
